/* The side navigation menu */
.left-block {
  background-color: #eee8f3;
}
.left-block .left-nav {
    padding: 10px;
}
.left-block .left-nav .subj {
      background-color: #7647a2;
      border-radius: 5px;
}
.left-block .left-nav .subj a {
        color: #fff;
        font-size: 17px;
        font-weight: 600;
}
.left-block .left-nav .subj a:hover {
          background-color: #bba3d0;
}
.left-block .left-nav .chart-btn {
      margin: 10px;
      font-size: 17px;
      font-weight: 500;
}
.right-block .book-list-div {
  margin-top: 30px;
}
.right-block .book-list-div .tile {
    -webkit-box-shadow: 5px 10px #551a8b;
            box-shadow: 5px 10px #551a8b;
    border-radius: 10px;
    background-color: #aa8cc5;
}
.right-block .book-list-div .tile .tile-content {
      margin: 20px;
      padding: 10px 10px 20px;
      color: #fff;
      text-align: left;
}
.right-block .book-list-div .tile .tile-content .img {
        width: 100%;
        height: 200px;
}
.right-block .book-list-div .tile .tile-content .title {
        text-decoration: underline;
}
.right-block .book-list-div .tile .tile-content .desc {
        font-size: 15px;
        font-weight: 500;
}
.right-block .book-list-div .tile .tile-content button {
        background-color: #551a8b;
}
.right-block .book-list-div .tile .tile-content button:hover {
          color: #fff;
          background-color: #bba3d0;
}
